// import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route,useLocation} from 'react-router-dom';
import { AuthProvider, useAuth } from './components/Login/AuthContext';
import LoginPage from './components/Login/LoginPage';
import SignUpPage from './components/Login/SignUpPage';
import ForgotPasswordPage from './components/Login/ForgotPasswordPage';
import CustomerApp from './CustomerApp';
import TechnicianApp from './TechnicianApp';
import AdminApp from './AdminApp';
import Header from './components/Header';
import Footer from './components/Footer';
import { useNavigate,Navigate } from 'react-router-dom'

const AppContent = () => {
    const { user, loading } = useAuth();
    const navigate = useNavigate();
    const location = useLocation();
    if (loading) {
        return <div style={{ minHeight: '100vh', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>Loading...</div>;
    }
    const isUserLoggedIn = !!user;
    const currentPathIsPublic = ['/login', '/signup', '/forgot-password'].includes(location.pathname);
    
    // Determine the user's intended base app component
    let AppRouter;
    if (user?.role === 'customer') AppRouter = CustomerApp;
    else if (user?.role === 'technician') AppRouter = TechnicianApp;
    else if (user?.role === 'admin') AppRouter = AdminApp;
    else AppRouter = LoginPage;

     return (
        <div style={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
            <Header user={user} />
            
            <main style={{ flexGrow: 1 }}>
                <Routes>
                    {/* 1. Static Public Routes */}
                    <Route path="/login" element={<LoginPage />} />
                    <Route path="/signup" element={<SignUpPage />} />
                    <Route path="/forgot-password" element={<ForgotPasswordPage />} />

                    {/* 2. Logged-in/Role Routes */}
                    {isUserLoggedIn ? (
                        /* If logged in, all paths go to the specific role's App */
                        <Route path="/*" element={<AppRouter />} />
                    ) : (
                        /* 3. Catch-all for Logged-out Users */
                        // If user is not logged in, force a redirect to /login if they try to access a private route
                        !currentPathIsPublic && <Route path="/*" element={<Navigate to="/login" replace />} />
                    )}

                    {/* 4. Final Fallback (If not caught above, direct to the App Router or Login) */}
                    {/* If the current path is /, and they are logged in, this renders their app */}
                    {isUserLoggedIn && <Route path="/" element={<AppRouter />} />}
                    {!isUserLoggedIn && <Route path="/" element={<LoginPage />} />}
                </Routes>
            </main>
            
            <Footer />
        </div>
  );
};
const App = () => (
    <Router>
        <AuthProvider>
            <AppContent />
        </AuthProvider>
    </Router>
);

export default App;